import {
	ADD_NEW_PILL,
	DELETE_CONDITION_DEBOUNCE,
	LIST_DELETE_CONDITION,
	LIST_REMOVE_GROUPBY,
	PROPERTIES_SET
} from '../constants';
import {Fragment, snabbdom} from '@servicenow/ui-renderer-snabbdom';
import {createCustomElement} from '@servicenow/ui-core';
import {createElementFromNode} from '@seismic/snabbdom-renderer';
import {emptyStateIcon} from './icons.js';
import {t} from 'sn-translate';
import ARIA_LIVE_MESSAGE from '@devsnc/sn-list-commons';
import debounce from 'lodash/debounce';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import packageJson from '../../package.json';
import styles from './styles.scss';

const deleteConditionDebounce = debounce(
	(item, {queryModel}, dispatch, index) => {
		const {count, fixedQueries} = queryModel;
		const fixedQueryLength = get(fixedQueries, 'length', 0);

		let deletedConditionIndex;

		if (count - 1 === fixedQueryLength) {
			deletedConditionIndex = -1;
		} else if (index > fixedQueryLength) {
			deletedConditionIndex = index - 1;
		} else {
			deletedConditionIndex = index;
		}

		dispatch(ARIA_LIVE_MESSAGE, {
			message: t(
				'List condition has been removed; your list has refreshed with new results'
			)
		});
		dispatch(PROPERTIES_SET, {deletedConditionIndex});
		dispatch(LIST_DELETE_CONDITION, {
			item,
			glideQuery: queryModel.glideQuery
		});
	},
	DELETE_CONDITION_DEBOUNCE
);

const getFixedQueries = properties => {
	const fixedQueries = get(properties, 'queryModel.fixedQueries', []);
	return [...fixedQueries].reduce((prev, curr) => {
		prev[curr] = prev[curr] || {isApplied: false};
		return prev;
	}, {});
};

const renderIcon = iconString => {
	const wrapper = document.createElement('SPAN');
	wrapper.innerHTML = iconString;
	const vnode = createElementFromNode(wrapper);
	return vnode.children[0];
};

export const renderNoFiltersApplied = () => {
	return (
		<div className="sn-empty-state">
			<div className="sn-empty-state-row">
				{renderIcon(emptyStateIcon.data)}
			</div>
			<div className="sn-empty-state-row -header">
				{t('No filters applied')}
			</div>
		</div>
	);
};

export const renderPills = (predicates, properties, dispatch) => {
	const separator = index => (
		<div className="sn-card-list-separator" ref={`divider-${index}`}>
			<span>{t('OR')}</span>
		</div>
	);
	return predicates.map(item => {
		let pillIndex = 0;
		let dividerIndex = 0;
		return item.subpredicates.map((second, index) => {
			let pills = null,
				divider = null;

			divider =
				index === 0 || second.subpredicates.length === 0
					? null
					: (divider = separator(dividerIndex++));

			pills = (
				<ul className="sn-card-list">
					{second.subpredicates.map(third =>
						third.subpredicates.map(item =>
							renderCardBody(item, pillIndex++, properties, dispatch)
						)
					)}
				</ul>
			);

			return (
				<Fragment>
					{divider}
					{pills}
				</Fragment>
			);
		});
	});
};

const renderCardBody = (item, index, properties, dispatch) => {
	const {
		hidePills,
		hidePanelConditionDelete,
		deletedConditionIndex
	} = properties;
	const isItemFixed = handleFixedItem(item, properties, dispatch);

	return (
		<li className="sn-card" key={item.id} aria-label={item.term_label}>
			<div className="sn-card-content">
				<span tooltip={item.term_label} tooltip-position="bottom">
					{item.term_label}
				</span>
			</div>
			{!isItemFixed && !hidePanelConditionDelete ? (
				<div className="sn-card-content -static">
					<button
						disabled={hidePills}
						index={`${index}`}
						type="button"
						className={`sn-card-close-btn ${hidePills ? 'is-disabled' : ''}`}
						on-click={() => {
							deleteConditionDebounce(item, properties, dispatch, index);
						}}
						hook-insert={vnode => {
							if (index === deletedConditionIndex) vnode.elm.focus();
						}}
						aria-label={t('Remove')}>
						<now-icon icon="close-fill" size="sm" />
					</button>
				</div>
			) : null}
		</li>
	);
};

const handleFixedItem = (item, {fixedQueriesMap}, dispatch) => {
	if (!isEmpty(fixedQueriesMap)) {
		const {field, operator, value} = item.term_label_fields;
		const query = `${field + operator + value}`;
		if (fixedQueriesMap[query] && !fixedQueriesMap[query].isApplied) {
			const newMap = {...fixedQueriesMap};
			newMap[query].isApplied = true;
			dispatch(PROPERTIES_SET, {fixedQueriesMap: newMap});
			return true;
		}
	}
	return false;
};

const renderGroupByPill = (groupByColumn, properties, dispatch) => {
	const {
		hidePills,
		queryModel,
		hidePanelConditionDelete,
		deletedConditionIndex
	} = properties;

	const groupByLabel = t(`Group by {0}`, groupByColumn);
	const index = queryModel ? queryModel.count - 1 : 0; // 0-based
	return (
		<li
			className="sn-card"
			key={`groudBy-${groupByColumn}-${index}`}
			aria-label={groupByLabel}>
			<div className="sn-card-content">{groupByLabel}</div>
			{!hidePanelConditionDelete ? (
				<div className="sn-card-content -static">
					<button
						disabled={hidePills}
						index={`${index}`}
						type="button"
						className={`sn-card-close-btn ${hidePills ? 'is-disabled' : ''}`}
						hook-insert={vnode => {
							if (index === deletedConditionIndex) vnode.elm.focus();
						}}
						on-click={() => dispatch(LIST_REMOVE_GROUPBY)}
						aria-label={t('Remove')}>
						<now-icon icon="close-fill" size="sm" />
					</button>
				</div>
			) : null}
		</li>
	);
};

export const renderGroupedByPills = (
	groupByConditions,
	properties,
	dispatch
) => {
	let pills = null;
	if (!isEmpty(groupByConditions)) {
		pills = (
			<ul className="sn-card-list">
				{groupByConditions.map(groupByCondition =>
					renderGroupByPill(groupByCondition, properties, dispatch)
				)}
			</ul>
		);
	}
	return pills;
};

export const view = ({properties}, {dispatch}) => {
	const {queryModel} = properties;

	return (
		<Fragment>
			{!queryModel || queryModel.count === 0 ? (
				renderNoFiltersApplied()
			) : (
				<div>
					{renderPills(
						get(queryModel, 'glideQuery.predicates', []),
						properties,
						dispatch
					)}
					{renderGroupedByPills(
						get(queryModel, 'glideQuery.group_by', []),
						properties,
						dispatch
					)}
				</div>
			)}
		</Fragment>
	);
};

createCustomElement('now-record-list-filter-panel', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		queryModel: {
			onChange(newValue, oldValue, dispatch) {
				const newCount = get(newValue, 'count', 0);
				const oldCount = get(oldValue, 'count', 0);

				if (newCount > oldCount) dispatch(ADD_NEW_PILL);
			}
		},
		/**
		 * deletedConditionIndex is used to set the focus on a condition after the user has
		 * removed a condition/criteria
		 */
		deletedConditionIndex: {
			default: -1
		},
		/**
		 * Fixed queries are used in the filter of related lists
		 */
		fixedQueriesMap: {
			computed: ({properties}) => getFixedQueries(properties)
		},
		hidePills: {
			default: false
		},
		hidePanelConditionDelete: {
			default: false
		}
	},
	styles,
	onConnect(host) {
		if (!window.listComponentRegistry) {
			window.listComponentRegistry = new WeakMap();
		}
		window.listComponentRegistry.set(host, packageJson.version);
	},
	onDisconnect(host) {
		if (window.listComponentRegistry) {
			window.listComponentRegistry.delete(host);
		}
	}
});
